import Vue from 'vue'
import Vuex from 'vuex'

import dictMapPart from './dict'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    appVersion: "1",
    appSubVersion: "1.0.0",
    freshTask: false,
    allTaskCount: {
      weixiuCounts: 0,
      weihuCounts: 0,
      xunchaCounts: 0
    },
    PREMISS: {
      createForm: "A010001",  //  
      modifyreason: "A010002",  //  
      delitem: "A010003",  //  
      sendForm: "A020000",  // 
      delegate: "A020001",  // 
      reDelegate: "A020002",  // 
      maintCheck: "A020003",  // 
      reveive: "A030001",  //  
      maintRecord: "A030003", //  
      refuse: "A030002",  // 
      saveMaintRecord: "A030005",  // 
      trans: "A030007",  // 
      viewMaintRecord: "A030008",  // 
    },
    dictMapPart: dictMapPart
  },
  mutations: {
    freshTaskFunc(state, payload) {
      state.freshTask = payload;
    },
    logout() {
      try {
        uni.clearStorageSync();
        uni.reLaunch({
          url: "/pages/login/index"
        })
      } catch (e) {
        // error
      }
    },
    getTaskCounts(state) {
      Vue.prototype.$u
          .post("myTask/getTaskCounts", {}).then(res => {
        if (res.code === 200) {
          if(res.data && Object.keys(res.data).length) {
            state.allTaskCount = res.data;
          }
        }
      }).catch(e => {
      }).finally(_ => {
      })
    }
  },
  actions: {}
})
export default store