export default class common {

  /**
   * toast
   * @param title
   */
  showToast(title) {
    uni.showToast({
      title: title,
      icon: 'none',
      duration: 3500
    })
  }

  /**
   *  
   * @param key
   */
  goPathForTaskDefKey(key) {
    this.showToast("");
    if (key) {
      let menuList = uni.getStorageSync('menuList');

      for (let item in menuList) {

        // key
        if (menuList[item].taskDefKey === key) {

          setTimeout(() => {

            uni.redirectTo({
              url: '/pages' + menuList[item].path
            });

          }, 500);

          break;
        }
      }
    } else {

      // key 
      setTimeout(() => {

        uni.navigateBack({
          delta: 1
        });

      }, 500);

    }

  }

  /**
   * 
   * @param tel
   * @returns {boolean}
   */
  isPhoneNumber(tel) {

    var reg = /^0?1[3|4|5|6|7|8][0-9]\d{8}$/;
    return reg.test(tel);

  }

  /**
   * 
   * @param email
   * @returns {boolean}
   */
  isEmail(email) {
    var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
    return reg.test(email)
  }

  dateFormat(fmt, date) {
    let ret;
    let opt = {
      "Y+": date.getFullYear().toString(),        // 
      "m+": (date.getMonth() + 1).toString(),     // 
      "d+": date.getDate().toString(),            // 
      "H+": date.getHours().toString(),           // 
      "M+": date.getMinutes().toString(),         // 
      "S+": date.getSeconds().toString()          // 
      // 
    };
    for (let k in opt) {
      ret = new RegExp("(" + k + ")").exec(fmt);
      if (ret) {
        fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
      }
      ;
    }
    ;
    return fmt;
  }

  toTree(data) {
    var map = {} //   id  KEY  map 
    data.forEach(function (item) {
      map[item.id] = item
    })
    var val = []
    data.forEach(function (item) {
      var parent = map[item.pid] // pid,mapid
      if (parent) {
        (parent.children || (parent.children = [])).push(item) // ,
      } else {
        val.push(item) // mapID, item val
      }
    })
    return val
  }

  queryStringToObj(str) {
    if (typeof str !== "string") {
      return null
          ;
      let s1 = str.split("?")[1];
      if (!s1) {
        return null
      }
      let arr = s1.split("&");
      let res = {};
      for (let i = 0; i < arr.length; i++) {
        let item = arr[i];
        let inner = item.split("=");
        res[inner[0]] = inner[1]
      }
      return res;
    }
  }
}









