import config from '../config'
// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: config.baseUrl,
		loadingText: '...',
		loadingTime: 800,
		// ......
	});
	
	//  
	const whiteRequest = ["sys/login"]
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorage
		// Storage
		uni.showLoading({
			title: "..."
		})
		const token = uni.getStorageSync('token');
		if(config.url.indexOf(whiteRequest) === -1 && !token) {
			uni.clearStorageSync();
			uni.switchTab({
			  url: "/pages/login/index"
			});
			return false
		} else {
			config.header.token = token;
		}
		
		// urlurlthis.$u.get(url)url
		// if(config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}
	
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		uni.hideLoading();
		if(res.code == 200) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res;
		} else if(res.code == 201) {
			// 201token
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				uni.switchTab({
				  url: "/pages/login/index"
				});
			}, 1500)
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			uni.showToast({
				title: res.message,
				icon: "none"
			});
			return false;
		}
	}
}

export default {
	install
}